import React from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { Route, withRouter, Switch } from 'react-router-dom';

/* import pages */
import { PageDev, Page404, PageError } from './pages/error';
import Home from './pages/home';
import CS394N from './pages/cs394n';

import './scss/Router.scss';

function AnimatedRouter({ location }) {
  return (
    <div>
      <TransitionGroup className="route-group">
        <CSSTransition
          key={location.key}
          timeout={{ enter: 500, exit: 500 }}
          classNames="route"
        >
          <section className="route-section">
            <Switch location={location}>
              <Route path="/" component={Home} exact />
              <Route path="/experience" component={PageDev} exact />
              <Route path="/experience/work" component={PageDev} exact />
              <Route path="/experience/pubs" component={PageDev} exact />
              <Route path="/projects" component={PageDev} exact />
              <Route path="/projects/undergrad" component={PageDev} exact />
              <Route path="/projects/grad" component={PageDev} exact />
              <Route path="/projects/personal" component={PageDev} exact />
              <Route path="/blog" component={PageDev} exact />
              <Route path="/fun" component={PageDev} exact />
              <Route path="/cs394n" component={CS394N} exact />
              <Route component={Page404} status={404} />
              <Route component={PageError} />
            </Switch>
          </section>
        </CSSTransition>
      </TransitionGroup>
    </div>
  )
}

export default withRouter(AnimatedRouter);
